.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
}

.title {
  font-size: 32px;
  font-weight: bold;
}

.subtitle {
  font-size: 18px;
}

.content-wrapper {
  padding: 0px 150px;
}

@media screen and (max-width: 1048px) {
  .content-wrapper {
    padding: 0px 50px;
  }
}


@media screen and (max-width: 840px) {
  .content-wrapper {
    padding: 0px 20px;
  }
}

.footer {
  background-color: #121418;
}